@use "global" as *;
/*  */
.swiper-button-prev::after {
  @include mq("md") {
    background-image: url(../images/common/campaign-prev.svg);
  }
}
/*  */
.swiper-button-next::after {
  @include mq("md") {
    background-image: url(../images/common/campaign-next.svg);
  }
}
.swiper-button-lock {
  display: inherit; /*  */
}
