@use "global" as *;
/*  */
.submit-btn {
  /* buttonCSS */
  background-color: transparent;
  border: none;
  cursor: pointer;
  outline: none;
  appearance: none;

  color: #fff;
  padding: 8px 24px;
  background-color: #3abec1;
  cursor: pointer; /*  */
}

.submit-btn[disabled] {
  background-color: $green;
  cursor: not-allowed; /*  */
}
