@use "global" as *;
// 
.top-button {
  position: fixed;
  bottom: -60px; /*  */
  right: 10px;
  color: $green;
  border: none;
  cursor: pointer;
  transition: bottom 0.6s ease; /*  */
  z-index: 10;
  opacity: 0;
}
.top-button:hover .top-button__icon {
  filter: invert(100%);
}
.top-button__icon {
  position: relative;
  width: 50px;
  height: 50px;
  @include mq("md") {
    width: 80px;
    height: 80px;
  }
}
.top-button.active {
  bottom: 6px; /*  */
  opacity: 1;
}
